.previewBox{
    cursor: pointer;
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    background-size: cover !important;
    background-position: center center !important;
    background-repeat: no-repeat !important;
    border-radius: 5px;
}

.previewBox svg{
    border-radius: 50%;
    height: 3rem;
    width: 3rem;
    border: solid 2px #d5d5d5;
    background-color: #d5d5d5;
}